<template>
  <div>
    <h1>ImageMagick WASM test</h1>

    <div>
      <input type="file" @change="loadImage" accept="image/*"/>
    </div>

    <div>
      <button
        v-for="op in SUPPORTED_OPERATIONS"
        type="button"
        @click="processImage(op)"
        :key="op"
      >
        {{ op }}
      </button>
    </div>

    <div>
      <canvas ref="canvas" width="800" height="600"></canvas>
    </div>

    <div>
      <h2>Image info</h2>

      <dl>
        <template v-for="(value, key) in imageInfo" :key="key">
          <template v-if="key != 'attributes'">
            <dt>{{ key }}</dt>
            <dd>{{ value }}</dd>
          </template>
          <template v-else>
            <template v-for="attr in value" :key="attr[0]">
              <dt>{{ attr[0] }}</dt>
              <dd>{{ attr[1] }}</dd>
            </template>
          </template>
        </template>
      </dl>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, reactive } from 'vue';
import {
  ImageMagick,
    initializeImageMagick,
    Magick,
    Quantum,
    ColorSpace,
    FilterType,
    OrientationType,
    PixelInterpolateMethod,
    VirtualPixelMethod,
} from '@imagemagick/magick-wasm';

const SUPPORTED_OPERATIONS = [
  'resize_0.5',
  'blur',
  'sharpen',
  'rotate_90',
  'charcoal',
  'oilPaint',
]

export default defineComponent({
  name: 'MagickWasmTest',
  setup() {
    const canvas = ref(null);
    const imageInfo = reactive({
      format: null,
      height: null,
      width: null,
      depth: null,
      backgroundColor: null,
      borderColor: null,
      channelCount: null,
      colorSpace: null,
      comment: null,
      filterType: null,
      hasAlpha: null,
      interpolate: null,
      orientation: null,
      page: null,
      quality: null,
      signature: null,
      virtualPixelMethod: null,
      attributes: [],
    });

    initializeImageMagick().then(() => {
      console.log(Magick.imageMagickVersion);
      console.log('Delegates:', Magick.delegates);
      console.log('Features:', Magick.features);
      console.log('Quantum:', Quantum.depth);
    });

    const setImageInfo = (image) => {
      imageInfo.format = image.format;
      imageInfo.width = image.width;
      imageInfo.height = image.height;
      imageInfo.depth = image.depth;
      imageInfo.backgroundColor = image.backgroundColor;
      imageInfo.borderColor = image.borderColor;
      imageInfo.channelCount = image.channelCount;
      imageInfo.colorSpace = ColorSpace[image.colorSpace];
      imageInfo.comment = image.comment;
      imageInfo.filterType = FilterType[image.filterType];
      imageInfo.hasAlpha = image.hasAlpha;
      imageInfo.interpolate = PixelInterpolateMethod[image.interpolate];
      imageInfo.orientation = OrientationType[image.orientation];
      imageInfo.page = image.page;
      imageInfo.quality = image.quality;
      imageInfo.virtualPixelMethod = VirtualPixelMethod[image.virtualPixelMethod];
      imageInfo.attributes = image.attributeNames.map((name) => [name, image.getAttribute(name)])
    }
    const loadImage = (e) => {
      const file = e.target.files[0];
      if (!file || !canvas.value) {
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        const buf = e.target.result;
        ImageMagick.read(new Uint8Array(buf), (image) => {
          setImageInfo(image);
          image.writeToCanvas(canvas.value);
        })
      }
      reader.readAsArrayBuffer(file);
    }

    const processImage = (operation) => {
      ImageMagick.readFromCanvas(canvas.value, (image) => {
        console.log('start: ' + operation)
        switch(operation) {
          case 'resize_0.5':
            image.resize(image.width / 2, image.height / 2);
            break;
          case 'blur':
            image.blur();
            break;
          case 'sharpen':
            image.sharpen();
            break;
          case 'rotate_90':
            image.rotate(90);
            break;
          case 'charcoal':
            image.charcoal();
            break;
          case 'oilPaint':
            image.oilPaint();
            break;
          default:
            console.log('unsupported operation')
        }
        setImageInfo(image);
        image.writeToCanvas(canvas.value);
        console.log('end: ' + operation)
      });
    }

    return {
      canvas,
      imageInfo,
      loadImage,
      processImage,
      SUPPORTED_OPERATIONS,
    };
  }
});
</script>
